import isPhone from 'validator/lib/isMobilePhone'
export default {
  data(){
    return{
      phoneactive:false,
      canclick:false,
      cantips:false,
      phone:''
    }
  },
  created(){
  },
  computed :{
    isphone(){
      return isPhone(this.phone,'zh-CN')
    }
  },
  methods:{
    change(){
      var len = this.phone.length;
      if(len>0){
        this.phoneactive=true;
      }else {
        this.phoneactive = false;
      }

      if(len == 11 && this.isphone){
        this.canclick = true;
        this.cantips= false;
      }else if (len ==11 && !this.isphone){
        this.canclick = false;
        this.cantips = true;
      }else {
        this.canclick = false;
        this.cantips = false;
      }
    },
    next(){//下一步
      if(this.isphone){
        this.$router.push('/register/'+this.phone+'/'+encodeURIComponent(this.$route.params.routername))
      }
    }
  }
}



